
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'Info',
components: {
XHeader,
Group,
XInput,
XButton
},
data() {
return {
username: '',
niname: '',
realname: '',
idcard: '',
phone: '',
qq: ''
};
},
created() {
this.getUserInfo();
},
methods: {
async getUserInfo() {
let res = await this.$http('/queryUserCommonInfo', {
body: {
userId: window.localStorage.getItem('userId'),
token: window.localStorage.getItem('token')
}
});
this.username = res.returnMap.userId;
this.niname = res.returnMap.userName;
this.realname = res.returnMap.realName;
this.idcard = res.returnMap.Id;
this.phone = res.returnMap.phone;
this.qq = res.returnMap.QQ;
},
async sub() {
if (
!this.niname ||
!this.realname ||
!this.idcard ||
!this.phone ||
!this.qq
) {
this.$vux.toast.show({
type: 'text',
text: '请填写完整的信息',
time: 1500
});
return;
}
let res = await this.$http('/updateUserInfoForFront', {
body: {
updateUserName: this.niname,
identification: this.idcard,
phoneNumber: this.phone,
qq: this.qq,
realName: this.realname
}
});
if (res.returnCode !== '0000') {
this.$vux.toast.show({
type: 'warn',
text: res.returnMessage,
time: 1500
});
} else {
this.$vux.toast.show({
type: 'success',
text: '修改成功',
time: 1500
});
this.$router.go(-1);
}
}
}
};



